<template>
  <div class="rec">
    <div class="rec-header">
      <input type="text" @focus="searchFn" v-model="searchValue" placeholder="搜索关键字"/>
      <span @click="onSearch" class="search-query"></span>
    </div>
    <div class="rec-content">
      <ul>
        <li v-for="item in myData">
          <div class="rec-content-title">{{item.title}}</div>
          <div class="rec-content-time clear">
            <span style="margin-right: 0.24rem;">{{item.createTime.split(' ')[0].replace(/-/g, '/')}}</span>
            <span>{{item.createTime.split(' ')[1]}}</span>
            <i @click="collect(item.id,2,$event)" v-show="item.favorite == 1" class="collect"></i>
            <i @click="collect(item.id,1,$event)" v-show="item.favorite == 0" class="cancel"></i>
          </div>
          <div class="rec-content-label">
            <a style="border: none;background: #f5fae5;">标签</a>
            <a v-for="i in item.tagInfo.split(',')">{{i}}</a>
          </div>
        </li>
      </ul>
    </div>
    <mt-spinner
      v-show="showSpinner"
      type="2"
      :size="60"
      color="#26a2ff"
      style="position:fixed; margin:auto;left:50%;top:50%;transform: translate(-50%, -50%);"
    >
    </mt-spinner>
    <mt-spinner
      v-show="showSpinner1"
      :size="50"
      color="#26a2ff"
      style="position:fixed; margin:0 auto; left:50%; bottom: 0.5rem;transform: translate(-50%, 0);"
      :type="1">
    </mt-spinner>
  </div>
</template>
<script>
  import {Spinner} from 'mint-ui';
  export default {
    created(){
      this.showSpinner = true;
      let _this = this;
      let url = `${this.$store.state.url}openApiCode=OPENAPI_000001&page=1&limit=10&userId=123456&keyWord=test1&sign=d48bf8d34cb1c044e05d2164e65ed4c5`;
      this.$store.dispatch('subAjax', {
        method: 'get',
        url: url,
        callback(res){
          try {
            _this.myData = res.data.page.list;
            _this.showSpinner = false;
          } catch (e) {

          }
        }
      })
    },
    component: {
      spinner: Spinner
    },
    data(){
      return {
        searchValue: '',
        myData: [],
        collection: false,
        showSpinner: true,
        showSpinner1: false
      }
    },
    methods: {
      searchFn(ev){
        ev.target.parentNode.lastElementChild.style.left = '9rem';
      },
      onSearch(){
        if (!this.searchValue) {
          this.$toast('请输入搜索内容');
          return false;
        }
        let url = `${this.$store.state.url}openApiCode=OPENAPI_000001&page=1&limit=10&userId=123456&keyWord=${this.searchValue}&sign=d48bf8d34cb1c044e05d2164e65ed4c5`;
        let _this = this;
        this.$store.dispatch('subAjax', {
          method: 'get',
          url: url,
          callback(res){
            try {
              _this.myData = res.data.page.list;
            } catch (e) {
            }
          }
        })
      },
      collect(articleInfo,state,ev){
        let _this = this;
        let url = `${this.$store.state.url1}openApiCode=OPENAPI_000006&userId=${this.syrGetUserInfo.getInfo()}&acticleId=${articleInfo}&status=${state}&sign=8586fe2a1ae3b85026d8d77a0430fb94`;
        this.$store.dispatch('subAjax', {
          method: 'get',
          url: url,
          callback(res){
            if(res){
              if(res.data.code == 0){
                setState()
              }else{
                _this.$toast('操作失败');
              }
            }
          }
        });
        function setState() {
          let myParentNode = ev.target.parentNode;
          let otherNode = myParentNode.getElementsByTagName('i');
          for (let i = 0; i < otherNode.length; i++) {
            if (otherNode[i].style.display == 'none') {
              otherNode[i].style.display = 'block';
              _this.$toast('收藏成功');
            } else {
              otherNode[i].style.display = 'none';
            }
          }
        }
      }
    },
    mounted(){
      let _this = this;
      let page = 2;
      let onoff = true;
      window.addEventListener('touchstart', function (ev) {
          let evt = ev || event;
          let startPagY = evt.touches[0].pageY;
          let direction = 0;
          window.addEventListener('touchmove', moveHandler, false);
          window.addEventListener('touchend', endHandler, false);
          function endHandler(ev) {
            if (direction <= 0) {
              return ;
            }
            let elem = document.documentElement || document.body;
            let overHeight = document.body.scrollTop + elem.clientHeight;
            if (overHeight + 40 > elem.offsetHeight && onoff) {
              _this.showSpinner1 = true;
              onoff = false;
              let url = `${_this.$store.state.url}openApiCode=OPENAPI_000001&page=${page}&limit=10&userId=${_this.syrGetUserInfo.getInfo()}&keyWord=&sign=d48bf8d34cb1c044e05d2164e65ed4c5`;
              _this.$store.dispatch('subAjax', {
                method: 'get',
                url: url,
                callback(res){
                  try {
                    _this.myData = _this.myData.concat(res.data.page.list);
                    _this.showSpinner1 = false;
                    if(res.data.page.list.length< 10){
                      onoff = false;
                    }else{
                      page ++;
                      onoff = true;
                    }
                  } catch (e) {
                    _this.$toast('操作失败');
                    setTimeout(function () {
                      _this.showSpinner1 = false;
                      onoff = true;
                    }, 1000);
                  }
                }
              })
            }else if(!onoff){
              _this.$toast('没有数据了');
            }
            window.removeEventListener('touchmove', moveHandler, false);
            window.removeEventListener('touchend', endHandler, false);
          }
          function moveHandler(ev) {
            let evt = ev || event;
            let endPagY = evt.touches[0].pageY;
            direction = startPagY - endPagY;
          }
        }
      );
    }
  }
</script>
<style scoped>
  .rec .rec-header {
    padding: 0.4rem 0.4rem 0.44rem 0.4rem;
    border-top: 0.03rem solid #d2d2d2;
    border-bottom: 0.03rem solid #d2d2d2;
    position: relative;
  }

  .rec .rec-header input {
    width: 9.2rem;
    height: 0.8rem;
    background: #fff;
    border: 0.03rem solid #535353;
    font-size: 0.32rem;
    border-radius: 0.2rem;
    text-align: center;
  }

  .rec .rec-header span {
    position: absolute;
    left: 3.55rem;
    top: 0.6rem;
    width: 0.493rem;
    height: 0.413rem;
    background: url(../assets/image/ic_search.png) no-repeat center;
    background-size: 100%;
  }

  .rec .rec-content ul li {
    /*height: 1.653rem;*/
    background: #fff;
    border-bottom: 0.03rem solid #d2d2d2;
    padding: 0.4rem 0.533rem 0.18rem 0.533rem;
  }

  .rec .rec-content .rec-content-title {
    font-size: 0.373rem;
    margin-bottom: 0.26rem;
    word-wrap: break-word;
    color: #000000;
  }

  .rec .rec-content .rec-content-time {
    font-size: 0.32rem;
    line-height: 0.453rem;
    margin-bottom: 0.24rem;
    color: #6e6e6e;
  }

  .rec .rec-content .rec-content-time i {
    float: right;
    width: 0.453rem;
    height: 0.453rem;
  }

  .rec .rec-content .rec-content-time .collect {
    background: url(../assets/image/ic_save_pre.png) no-repeat center;
    background-size: 100%;
  }

  .rec .rec-content .rec-content-time .cancel {
    background: url(../assets/image/ic_save.png) no-repeat center;
    background-size: 100%;
  }

  .rec .rec-content .rec-content-label {
    font-size: 0.24rem;
    color: #a2cf00;
  }

  .rec .rec-content .rec-content-label a {
    display: inline-block;
    padding: 0.05rem 0.2rem 0.05rem 0.2rem;
    border: 0.018rem solid #707070;
    border-radius: 0.2rem;
    margin-right: 0.226rem;
  }

</style>




